import Config from './Config';
import Canvas from './Canvas';

class MinuteView {
  constructor () {
    this.config = Config.getInstance().calendar;
    this.ctx = Canvas.getCtx('calendar');
  }

  render (minute) {
    this.ctx.beginPath();

    // if it's not middle border,
    // draw it dotted
    // if (minute !== this.config.minute.minutes / 2) {
    //   this.ctx.setLineDash([1, 1]);
    // }

    this.ctx.strokeStyle = this.config.style.minute.borderColor;
    // this.ctx.moveTo(0, 0.5);
    // this.ctx.lineTo(this.config.minute.width, 0.5);

    this.ctx.strokeRect(0.5, 0.5, this.config.minute.width, this.config.minute.height);

    //this.ctx.setLineDash([]);
    this.ctx.closePath();
  }
}

export default MinuteView;


